import { clsx } from 'clsx';
import { Slot } from '@radix-ui/react-slot';
import { ReactNode } from 'react';

export interface TextProps {
    size?: 'sm' | 'md' | 'lg';
    children: ReactNode;
    asChild?: boolean;
    className?: string; // Permite adição de outras classes
}

export function Text({ size = 'md', children, asChild, className }: TextProps) {
    const Comp = asChild ? Slot : 'span';
    
    return (
        <Comp 
            className={clsx(
                'text-gray-100 font-sans',
                {
                    'text-xs': size == 'sm',
                    'text-sm': size == 'md',
                    'text-md': size == 'lg',
                },
                className, // Colocado no final para sobreescrever as classes
            )}
        >
            {children}
        </Comp>
    )
}